@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600,700);
@import url(https://fonts.googleapis.com/css?family=Catamaran:400,800);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
    display: flex;
    position: relative;
    width: 800px;
    height: 850px;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    flex-direction: column;
    border: 2px solid #262626;
    border-radius: 30px;
    overflow: hidden;
}

.header_box {
    display: flex;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    height: 70px;
    width: 900px;
    background-color: rebeccapurple;
    color: white;
    font-size: 25px;
    font-weight: bold;
    align-items: center;
    justify-content: center;
}

.home {
    display: flex;
    flex-direction: column;
    position: relative;
    top: 110px;
    align-items: center;
}

#quiz_img {
    border-radius: 20px;
    width: 400px;
}

#home_box {
    margin-top: 20px;
    text-align: center;
}

#home_box > ul {
    font-size: 14px;
    list-style-type: square;
    line-height: 2;
    text-align: left;
}

.info_box {
    display: flex;
    flex-direction: column;
    width: 400px;
    height: 200px;
    background-color: rebeccapurple;
    border-radius: 20px;
    margin-bottom: 50px;
    align-items: center;
    justify-content: center;
}

#info_selector {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align-last: center;
    align-items: center;
    margin-top: 15px;
}

.student_info {
    width: 320px;
    display: flex;
    justify-content: space-between;
    margin: 0 10px 15px 10px;
}

.info {
    width: 100px;
    font-size: 16px;
}

.student_number {
    margin-right: 0;
}

.student_name {
    width: 312px;
    font-size: 18px;
    margin-bottom: 15px;
}

#start_btn {
    width: 200px;
    height: 50px;
    border: 1px solid #262626;
    border-radius: 10px;
    background-color: white;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
}

.contents {
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100%;
    background-color: rebeccapurple;
    text-align: center;
    justify-content: center;
}

.contents_box {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 650px;
    height: 600px;
    background-color: white;
    border-radius: 20px;
}

.problem_num {
    text-align: left;
    margin-bottom: 50px;
    padding-left: 20px;
}

.problem_title {
    margin-bottom: 50px;
}

.answers {
    width: 95%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    text-align: left;
}

.answers > div {
    margin-top: 25px;
    border: 1px solid rebeccapurple;
    border-radius: 100px;
    padding: 15px 0 15px 30px ;
    cursor: pointer;
}

.next_btn {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 630px;
    width: 110px;
    height: 45px;
    border: 1px solid mediumpurple;
    border-radius: 10px;
    background-color: rebeccapurple;
    color: white;
    cursor: pointer;
}

.result_btn {
    -webkit-animation-name: result_btn;
            animation-name: result_btn;
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
    -webkit-animation-timing-function: unset;
            animation-timing-function: unset;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-animation-iteration-count: 5;
            animation-iteration-count: 5;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-fill-mode: none;
            animation-fill-mode: none;
    -webkit-animation-play-state: running;
            animation-play-state: running;
    font-weight: bold;
}

@-webkit-keyframes result_btn {
    from {
        width: 120px;
        height: 55px;
    }
    to {
        width: 110px;
        height: 45px;
    }
}

@keyframes result_btn {
    from {
        width: 120px;
        height: 55px;
    }
    to {
        width: 110px;
        height: 45px;
    }
}

.result {
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100%;
    justify-content: center;
    align-items: center;
}

.result_box {
    width: 600px;
    height: 645px;
    background-color: rebeccapurple;
    border-radius: 20px;
    margin-top: 20px;
    text-align: center;
}

.result_box .inner {
    position: relative;
    left: 50%;
    top: 95px;
    transform: translateX(-50%);
    width: 400px;
    height: 300px;
    border-radius: 20px;
    background-color: white;
}

.result_box .inner > h2 {
    position: relative;
    margin: 0;
    top: 40px;
}

.result_box .inner > div:nth-child(2) {
    position: relative;
    top: 85px;
    font-weight: bold;
    font-size: 55px;
    color: red;
}

.result_box .inner > div:nth-child(3) {
    position: relative;
    top: 140px;
    font-weight: bold;
    font-size: 20px;
    color: blue;
}

#home_btn {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 650px;
    width: 300px;
    height: 50px;
    border: 1px solid mediumpurple;
    border-radius: 10px;
    background-color: white;
    font-weight: bold;
    font-size: 16px;
    cursor: pointer;
}

@media screen and (max-width: 400px) {
    .container {
        width: 95%;
        height: 715px;
        top: 10px;
    }

    #quiz_img {
        width: 300px;
    }

    #info_selector {
        width: 300px;
        display: flex;
        justify-content: space-between;
        margin: 25px 10px;
    }

    .info_box {
        width: 330px;
        height: 170px;
        margin-bottom: 20px;
    }

    .student_info {
        width: 270px;
    }

    .info {
        width: 85px;
        font-size: 15px;
    }

    .student_name {
        width: 265px;
    }

    .contents_box {
        width: 320px;
        height: 540px;
    }

    .answers > div {
        padding: 10px 0 10px 30px ;
        cursor: pointer;
    }

    .next_btn {
        top: 560px;
        width: 110px;
        height: 45px;
    }

    .result_box {
        width: 315px;
        height: 480px;
        margin-top: 50px;
    }

    .result_box .inner {
        width: 285px;
        top: 55px;
    }

    #home_btn {
        top: 555px;
        width: 205px;
        height: 45px;
    }
}
.error-container {
    text-align: center;
    font-size: 106px;
    font-family: 'Catamaran', sans-serif;
    font-weight: 800;
    margin: 70px 15px;
}
.error-container > span {
    display: inline-block;
    position: relative;
}
.error-container > span.four {
    width: 136px;
    height: 43px;
    left: 3px;
    margin-right: 5px;
    border-radius: 999px;
    background:
            linear-gradient(140deg, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.07) 43%, transparent 44%, transparent 100%),
            linear-gradient(105deg, transparent 0%, transparent 40%, rgba(0, 0, 0, 0.06) 41%, rgba(0, 0, 0, 0.07) 76%, transparent 77%, transparent 100%),
            linear-gradient(to right, #d89ca4, #e27b7e);
}
.error-container > span.four:before,
.error-container > span.four:after {
    content: '';
    display: block;
    position: absolute;
    border-radius: 999px;
}
.error-container > span.four:before {
    width: 43px;
    height: 156px;
    left: 60px;
    bottom: -43px;
    background:
            linear-gradient(128deg, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.07) 40%, transparent 41%, transparent 100%),
            linear-gradient(116deg, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.07) 50%, transparent 51%, transparent 100%),
            linear-gradient(to top, #99749D, #B895AB, #CC9AA6, #D7969E, #E0787F);
}
.error-container > span.four:after {
    width: 137px;
    height: 43px;
    transform: rotate(-49.5deg);
    left: -18px;
    bottom: 36px;
    background: linear-gradient(to right, #99749D, #B895AB, #CC9AA6, #D7969E, #E0787F);
}

.error-container > span.zero {
    vertical-align: text-top;
    width: 156px;
    height: 156px;
    border-radius: 999px;
    background: linear-gradient(-45deg, transparent 0%, rgba(0, 0, 0, 0.06) 50%,  transparent 51%, transparent 100%),
    linear-gradient(to top right, #99749D, #99749D, #B895AB, #CC9AA6, #D7969E, #ED8687, #ED8687);
    overflow: hidden;
    -webkit-animation: bgshadow 5s infinite;
            animation: bgshadow 5s infinite;
}
.error-container > span.zero:before {
    content: '';
    display: block;
    position: absolute;
    transform: rotate(45deg);
    width: 90px;
    height: 90px;
    background-color: transparent;
    left: 0px;
    bottom: 0px;
    background:
            linear-gradient(95deg, transparent 0%, transparent 8%, rgba(0, 0, 0, 0.07) 9%, transparent 50%, transparent 100%),
            linear-gradient(85deg, transparent 0%, transparent 19%, rgba(0, 0, 0, 0.05) 20%, rgba(0, 0, 0, 0.07) 91%, transparent 92%, transparent 100%);
}

.error-container > span.zero:after {
    content: '';
    display: block;
    position: absolute;
    border-radius: 999px;
    width: 70px;
    height: 70px;
    left: 44px;
    bottom: 43px;
    background: #FDFAF5;
    box-shadow: -2px 2px 2px 0px rgba(0, 0, 0, 0.1);
}

.error-container > span.eight {
    vertical-align: text-top;
    width: 100px;
    height: 100px;
    left: 9px;
    border-radius: 999px;
    background:
            linear-gradient(140deg, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.07) 43%, transparent 44%, transparent 100%),
            linear-gradient(105deg, transparent 0%, transparent 40%, rgba(0, 0, 0, 0.06) 41%, rgba(0, 0, 0, 0.07) 76%, transparent 77%, transparent 100%),
            linear-gradient(to right, #d89ca4, #e27b7e);
}
.error-container > span.eight:before,
.error-container > span.eight:after {
    content: '';
    display: block;
    position: absolute;
    border-radius: 999px;
}
.error-container > span.eight:before {
    width: 115px;
    height: 115px;
    border: 999px;
    left: -7px;
    bottom: -64px;
    background:
            linear-gradient(128deg, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.07) 40%, transparent 41%, transparent 100%),
            linear-gradient(116deg, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.07) 50%, transparent 51%, transparent 100%),
            linear-gradient(to top, #99749D, #B895AB, #CC9AA6, #D7969E, #E0787F);
}
.error-container > span.eight:after {
    content: '';
    display: block;
    position: absolute;
    border-radius: 999px;
    width: 40px;
    height: 40px;
    left: 31px;
    bottom: 32px;
    background: #FDFAF5;
    box-shadow: -2px 2px 2px 0px rgba(0, 0, 0, 0.1);
}

.error-container > span.eight >span.eight_ {
    content: '';
    display: block;
    position: absolute;
    border-radius: 999px;
    width: 46px;
    height: 46px;
    left: 29px;
    bottom: -29px;
    background: #FDFAF5;
    box-shadow: -2px 2px 2px 0px rgba(0, 0, 0, 0.1);
}

.screen-reader-text {
    position: absolute;
    top: -9999em;
    left: -9999em;
}

@-webkit-keyframes bgshadow {
    0% {
        box-shadow: inset -160px 160px 0px 5px rgba(0, 0, 0, 0.4);
    }
    45% {
        box-shadow: inset 0px 0px 0px 0px rgba(0, 0, 0, 0.1);
    }
    55% {
        box-shadow: inset 0px 0px 0px 0px rgba(0, 0, 0, 0.1);
    }
    100% {
        box-shadow: inset 160px -160px 0px 5px rgba(0, 0, 0, 0.4);
    }
}

@keyframes bgshadow {
    0% {
        box-shadow: inset -160px 160px 0px 5px rgba(0, 0, 0, 0.4);
    }
    45% {
        box-shadow: inset 0px 0px 0px 0px rgba(0, 0, 0, 0.1);
    }
    55% {
        box-shadow: inset 0px 0px 0px 0px rgba(0, 0, 0, 0.1);
    }
    100% {
        box-shadow: inset 160px -160px 0px 5px rgba(0, 0, 0, 0.4);
    }
}

