.container {
    display: flex;
    position: relative;
    width: 800px;
    height: 850px;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    flex-direction: column;
    border: 2px solid #262626;
    border-radius: 30px;
    overflow: hidden;
}

.header_box {
    display: flex;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    height: 70px;
    width: 900px;
    background-color: rebeccapurple;
    color: white;
    font-size: 25px;
    font-weight: bold;
    align-items: center;
    justify-content: center;
}

.home {
    display: flex;
    flex-direction: column;
    position: relative;
    top: 110px;
    align-items: center;
}

#quiz_img {
    border-radius: 20px;
    width: 400px;
}

#home_box {
    margin-top: 20px;
    text-align: center;
}

#home_box > ul {
    font-size: 14px;
    list-style-type: square;
    line-height: 2;
    text-align: left;
}

.info_box {
    display: flex;
    flex-direction: column;
    width: 400px;
    height: 200px;
    background-color: rebeccapurple;
    border-radius: 20px;
    margin-bottom: 50px;
    align-items: center;
    justify-content: center;
}

#info_selector {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align-last: center;
    align-items: center;
    margin-top: 15px;
}

.student_info {
    width: 320px;
    display: flex;
    justify-content: space-between;
    margin: 0 10px 15px 10px;
}

.info {
    width: 100px;
    font-size: 16px;
}

.student_number {
    margin-right: 0;
}

.student_name {
    width: 312px;
    font-size: 18px;
    margin-bottom: 15px;
}

#start_btn {
    width: 200px;
    height: 50px;
    border: 1px solid #262626;
    border-radius: 10px;
    background-color: white;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
}

.contents {
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100%;
    background-color: rebeccapurple;
    text-align: center;
    justify-content: center;
}

.contents_box {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 650px;
    height: 600px;
    background-color: white;
    border-radius: 20px;
}

.problem_num {
    text-align: left;
    margin-bottom: 50px;
    padding-left: 20px;
}

.problem_title {
    margin-bottom: 50px;
}

.answers {
    width: 95%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    text-align: left;
}

.answers > div {
    margin-top: 25px;
    border: 1px solid rebeccapurple;
    border-radius: 100px;
    padding: 15px 0 15px 30px ;
    cursor: pointer;
}

.next_btn {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 630px;
    width: 110px;
    height: 45px;
    border: 1px solid mediumpurple;
    border-radius: 10px;
    background-color: rebeccapurple;
    color: white;
    cursor: pointer;
}

.result_btn {
    animation-name: result_btn;
    animation-duration: 0.3s;
    animation-timing-function: unset;
    animation-delay: 0s;
    animation-iteration-count: 5;
    animation-direction: alternate;
    animation-fill-mode: none;
    animation-play-state: running;
    font-weight: bold;
}

@keyframes result_btn {
    from {
        width: 120px;
        height: 55px;
    }
    to {
        width: 110px;
        height: 45px;
    }
}

.result {
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100%;
    justify-content: center;
    align-items: center;
}

.result_box {
    width: 600px;
    height: 645px;
    background-color: rebeccapurple;
    border-radius: 20px;
    margin-top: 20px;
    text-align: center;
}

.result_box .inner {
    position: relative;
    left: 50%;
    top: 95px;
    transform: translateX(-50%);
    width: 400px;
    height: 300px;
    border-radius: 20px;
    background-color: white;
}

.result_box .inner > h2 {
    position: relative;
    margin: 0;
    top: 40px;
}

.result_box .inner > div:nth-child(2) {
    position: relative;
    top: 85px;
    font-weight: bold;
    font-size: 55px;
    color: red;
}

.result_box .inner > div:nth-child(3) {
    position: relative;
    top: 140px;
    font-weight: bold;
    font-size: 20px;
    color: blue;
}

#home_btn {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 650px;
    width: 300px;
    height: 50px;
    border: 1px solid mediumpurple;
    border-radius: 10px;
    background-color: white;
    font-weight: bold;
    font-size: 16px;
    cursor: pointer;
}

@media screen and (max-width: 400px) {
    .container {
        width: 95%;
        height: 715px;
        top: 10px;
    }

    #quiz_img {
        width: 300px;
    }

    #info_selector {
        width: 300px;
        display: flex;
        justify-content: space-between;
        margin: 25px 10px;
    }

    .info_box {
        width: 330px;
        height: 170px;
        margin-bottom: 20px;
    }

    .student_info {
        width: 270px;
    }

    .info {
        width: 85px;
        font-size: 15px;
    }

    .student_name {
        width: 265px;
    }

    .contents_box {
        width: 320px;
        height: 540px;
    }

    .answers > div {
        padding: 10px 0 10px 30px ;
        cursor: pointer;
    }

    .next_btn {
        top: 560px;
        width: 110px;
        height: 45px;
    }

    .result_box {
        width: 315px;
        height: 480px;
        margin-top: 50px;
    }

    .result_box .inner {
        width: 285px;
        top: 55px;
    }

    #home_btn {
        top: 555px;
        width: 205px;
        height: 45px;
    }
}